 /*Dropdown Bar Css*/

 .col-div-8
 {
    width: 4%;
    height: 36px;
    display: inline-block;
    display:flex;
    align-items:center;
    justify-content: center;
    
 }

.user-file{
   position:absolute;
   bottom:0;
   outline:none;
   color:transparent;
   width:100%;
   box-sizing: border-box;
   
 }

 .user-file::-webkit-file-upload-button{
   visibility: hidden;
 }


 .profile-pic{
   height: 3rem;
   width: auto;
   cursor: pointer;
   align-items: center;
   border-radius: 2rem;
   margin:auto;
   position:relative;
 }

.fa-lg{
   font-size: 1em;
   width: auto;
   cursor: pointer;
   align-items: center;
   border-radius: 2rem;
   margin:auto;
   position:relative;
 };


 .dropdown {
   display: inline-block;
   position: relative;
 }

 button:hover{
   background-color:#ddd;
 }
 
 .dropdown-options {
   display: none;
   position: absolute;
   overflow: auto;
   background-color:#fff;
   border-radius:5px;
   box-shadow: 0px 10px 10px 0px rgba(0,0,0,0.4);
 }
 
 .dropdown:hover .dropdown-options {
   display: block;
 }
 
 .dropdown-options a {
   display: block;
   color: #000000;
   padding: 5px;
   text-decoration: none;
   padding:20px 40px;
 }
 
 .dropdown-options a:hover {
   color: #0a0a23;
   background-color: #ddd;
   border-radius:5px;
 }

